<template>
  <div class="dashboard-container">
    <el-row :gutter="20">
      <el-col :span="8">
        <el-card>
          <template #header>
            <div class="clearfix">
              <span>用户数据</span>
            </div>
          </template>
          <div class="user-stats" :key="k" v-for="k in userdata">
            <el-avatar size="large" :src="DefaultImg"></el-avatar>
            <div class="user-info">
              <h3>{{k.name}} <a @click="changeInfo(k.name)" style="color: #409EFF;cursor: pointer">修改密码</a></h3>
              <template v-if="k.name===name">
                <p>权限：{{k.permission}}  <a @click="deleteInfo(k.name)" style="color: #ec001d;cursor: pointer">注销用户</a></p>
              </template>
              <template v-if="k.name!==name">
                <p>权限：{{k.permission}}  <a @click="deleteInfo(k.name)" style="color: #ec001d;cursor: pointer">删除用户</a></p>
              </template>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="8">
        <el-card v-show="isAdmin">
          <template #header>
            <div class="clearfix">
              <span>管理商品</span>
            </div>
          </template>
          <div class="system-status">
<!--            for循环打印商品数据-->
            <div class="shop-item" :key="item.id" v-for="item in shopdata">
              <div class="shop-item-info" >
                <el-avatar size="large" :src="item.image"></el-avatar>
                <div class="shop-item-text">
                  <h3>{{item.name}}</h3>
                  <p>价格：{{item.price}}</p>
                  <p>库存：{{item.value}}</p>
                  <p>描述：{{item.description}}</p>
                </div>
                <div class="shop-item-button">
                  <el-button type="primary" @click="updateShop(item.name)">修改</el-button>
                  <el-button type="danger" @click="deleteShop(item.name)">删除</el-button>
                </div>
              </div>
            </div>
            <br>
            <div class="shop-item-button">
              <el-button type="primary" @click="insertShop()">添加商品</el-button>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="8">
        <el-card>
          <template #header>
            <div class="clearfix">
              <span>版本更新动态</span>
            </div>
          </template>
          <div class="latest-news">
            <el-timeline>
              <el-timeline-item
                  v-for="(item, index) in versionNews"
                  :key="index"
                  :timestamp="item.version"
                  placement="top">
                {{ item.text }}
              </el-timeline-item>
            </el-timeline>
          </div>
<!--          button居中 提升权限-->
          <div style="justify-content: center;display: flex;" v-show="!isAdmin">
            <el-button type="primary" @click="changePermission()">提升权限</el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import config from "@/class/config";
import {MD5} from "crypto-js";
export default {
  name: 'DataLocal',
  data() {
    return {
      shopdata:[

      ],
      input:"",
      permission:"",
      isAdmin:false,
      DefaultImg:"data:image/jpeg;base64,",
      userdata:[
      ],
      versionNews:[
        {
          version: 'v1.2.0',
          text: 'V1版本最后一次更新，已被标记为：弃用。'
        },
        {
          version: 'v2.0.0',
          text: '前后端全栈重构。'
        },
          {
            version: 'v2.0.1',
            text: '前端顺利切换至Vue3 + Element Plus + VueX + VueRouter技术栈。'
          },
          {
            version: 'v2.1.0',
            text: '后端顺利切换至Springboot + RocketMQ + SpringJDBC + Mysql技术栈。'
          },
          {
            version: 'v2.1.1',
            text: '前后端分离，前后端数据交互，后端user、login、token等模块完成微服务架构并上线测试。'
          },
          {
            version: 'v2.2.0',
            text: '修复部分前端后端交互问题。'
          },
          {
            version: 'v2.2.1',
            text: '测试上线购物车功能。'
          },
          {
            version: 'v2.2.2',
            text: '上线结算支付功能，修复购物车灰度测试中出现的无法正常添加删除Bug问题。'
          },
          {
            version: 'v2.2.3',
            text: '修复部分前端后端交互问题。'
          },
          {
            version: 'v2.2.4',
            text: '对提升权限页面逻辑进行修改，同时移动入口至DashBoard。'
          },
          {
            version: 'v2.2.5',
            text: '当前版本，灰度测试支付系统稳定性。'
          },

      ],
      NotAdmin:false
    }
  },
  mounted() {
    var token = this.$cookies.get("token");
    if(token==null||token===""){
      this.$message.error("未检测到登录态");
      this.$cookies.remove("token");
      this.$router.push("/user/login");
      return;
    }
    var permission = this.$cookies.get("permission");
    if(permission!=="user"){
      this.isAdmin = true;
    }
    this.$axios.post(config.baseUrl+config.apiUrl+config.api.selectUser,{},{headers:{
      'token':token
      }}).then(res=>{
        if(res.data.Code==200){
          this.userdata = res.data.users;
          if(this.userdata.length > 1 && this.isAdmin === false){
          //   彻底刷新页面
            this.$router.go(0);
          }
        }else{
          this.$message.error(res.data.Response);
        }
    })

    this.$axios.post(config.baseUrl+config.apiUrl+config.api.ShopInfo,{}).then(res=>{
      if(res.data.Code==200){
        this.shopdata = res.data.elements;
      }else{
        this.$message.error(res.data.Response);
      }
    })
  },
  props: {
    name:String
  },
  methods: {
    changeInfo(name){
    //   input
      var token = this.$cookies.get("token");
      this.$prompt('请输入新密码', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^[a-zA-Z0-9]{6,20}$/,
        inputErrorMessage: '密码必须为6-20位字母或数字'
      }).then(({ value }) => {
        this.$axios.post(config.baseUrl+config.apiUrl+config.api.updateUser+"?name="+name+"&newpassword="+MD5(value).toString(),{},{headers:{
          'token':token
        }}).then(res=>{
          if(res.data.Code==200){
            this.$message({
              type: 'success',
              message: '修改成功!'
            });
            if(name===this.name){
              this.$cookies.remove("token");
              this.$cookies.remove("permission");
              this.$router.push("/user/login");
              return;
            }
            this.$router.go(0);
          }else{
            this.$message.error(res.data.Response);
          }
          })
      }).catch(()=> {
        this.$message.info('取消输入');
      })
    },
    deleteInfo(name){
      var token = this.$cookies.get("token");
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios.post(config.baseUrl+config.apiUrl+config.api.deleteUser+"?name="+name,{},{headers:{
          'token':token
        }}).then(res=>{
          if(res.data.Code==200){
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            if(name===this.name){
              this.$cookies.remove("token");
              this.$cookies.remove("permission");
              this.$router.push("/user/login");
              return;
            }
            this.$router.go(0);
          }else{
            this.$message.error(res.data.Response);
          }
        })
      }).catch(()=> {
        this.$message.info('取消输入');
      })
    },
    deleteShop(name){
      var token = this.$cookies.get("token");
      this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios.post(config.baseUrl+config.apiUrl+config.api.deleteShop+"?name="+name,{},{headers:{
          'token':token
        }}).then(res=>{
          if(res.data.Code==200){
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            this.$router.go(0);
          }else{
            this.$message.error(res.data.Response);
          }
        })
      })
    },
    updateShop(name){
      this.$router.push({name:"changeShop",query:{name:name}});
    },
    insertShop(){
      this.$router.push("/shop/insert");
    },
    changePermission(){
      this.$router.push("/user/code");
    }
  }
}
</script>

<style scoped>
.dashboard-container {
  padding: 20px;
}
.user-stats {
  display: flex;
  align-items: center;
}
.user-info {
  margin-left: 20px;
}
.system-status{
  text-align: center;
}
.shop-item{
  padding-top: 10px;
  padding-bottom: 10px;
  margin-top: 30px;
  border-radius: 10px;
  background-color: rgba(218, 218, 218, 0.49);
}
</style>
